import { UserService } from "./../shared/user/user.service";
import { Rating } from "./../shared/rating/rating";
import { Task } from "../shared/task/task";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Component, OnInit, ViewChild } from "@angular/core";
import { RatingService } from "../shared/rating/rating.service";
import * as moment from "moment";
import { BasicFunctions } from "../shared/utility/basic-functions";

@Component({
  selector: "app-rating",
  templateUrl: "./rating.component.html",
  styleUrls: ["./rating.component.scss"]
})
export class RatingComponent implements OnInit {
  @ViewChild("ratingModal") ratingModal;
  public ratings: BehaviorSubject<Array<Rating>>;
  public ratingCount: number;
  public ratingDetails: Rating;
  public ratingText: string;
  public tasks: Object;
  public users: Object;

  constructor(
    public ratingService: RatingService,
    private userService: UserService,
    public basicFunctions: BasicFunctions
  ) {
    this.ratingCount = 0;
    this.ratings = this.ratingService.ratings;
    this.ratingText = "";
    this.ratingDetails = new Rating();
    this.tasks = {};
    this.users = {};
  }

  ngOnInit() {
    this.ratingService.getRatings().subscribe();
    this.ratings.subscribe(data => {
      this.ratingCount = data.length;
    });
    this.ratingService.tasks.subscribe(tasks => {
      this.tasks = tasks;
    });
    this.users = this.arrayToObject(this.userService.userBucket, "userId");
  }

  convertDateTime(dateTime, format) {
    if (dateTime === 0 || dateTime === undefined) {
      return "-";
    }
    return moment(new Date(dateTime)).format(format);
  }

  getUserNameByUserId(userId) {
    return this.userService.getUserNameByUserId(userId);
  }

  arrayToObject = (array, keyField) =>
    array.reduce((obj, item) => {
      obj[item[keyField]] = item;
      return obj;
    }, {});

  openModal(rating) {
    this.ratingDetails = rating;
    this.ratingModal.show();
  }

  closeModal() {
    this.ratingModal.hide();
  }

  getRatingText(stars) {
    switch (stars) {
      case 1:
        return (this.ratingText = "Very Poor!");
      case 2:
        return (this.ratingText = "Poor");
      case 3:
        return (this.ratingText = "Fairly Okay");
      case 4:
        return (this.ratingText = "Good Job");
      case 5:
        return (this.ratingText = "Excellent");
      default:
        return (this.ratingText = "");
    }
  }
}



